.header{
    background-color: #131921;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 100;
}

.header_logo{
    width: 100px;
    margin-top: 18px;
    margin-left: 10px;
    margin-right: 10px;
    object-fit: contain;
}

.header_nav {
display: flex;
justify-content: space-evenly;
}

.header_link {
    color: white;
    text-decoration: none;
}

.header_option{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
}
.header_optionBasket {
    display: flex;
    align-items: center;
}

.header_basketCount {
    margin-left: 10px;
    margin-right: 10px;
}
.header_optionLineOne {
    font-size: 10px;
}

.header_optionLineTwo {
    font-size: 13px;
    font-weight: 800;
}

.header_search {
    display: flex;
    flex: 1;
}

.header_searchInput {
    border: none;
    width: 100%;
    padding: 10px;
    height: 12px;
}

.header_searchIcon {
    padding: 5px;
    height: 22px !important;

background-color: orange;
}

